import { useState } from "react";

import Radio from "@material-ui/core/Radio";
import InputLabel from "@material-ui/core/InputLabel";
import styled from "styled-components";

const HowBehavedSection = styled.section``;

function NaughtyChecked() {
  return <section>Naughty Checked</section>;
}

function NiceChecked() {
  return <section>Nice Checked</section>;
}

function VeryNiceChecked() {
  return <section>Very Nice Checked</section>;
}

function HowBehaved() {
  const [howBehaved, setHowBehaved] = useState("");
  return (
    <HowBehavedSection>
      <InputLabel>Naughty</InputLabel>
      <Radio
        value="naughty"
        onChange={event => setHowBehaved(event.target.value)}
        checked={howBehaved === "naughty"}
      />
      <InputLabel>Nice</InputLabel>
      <Radio
        value="nice"
        onChange={event => setHowBehaved(event.target.value)}
        checked={howBehaved === "nice"}
      />
      <InputLabel>Very Nice</InputLabel>
      <Radio
        value="veryNice"
        onChange={event => setHowBehaved(event.target.value)}
        checked={howBehaved === "veryNice"}
      />{" "}
      {howBehaved === "naughty" && <NaughtyChecked />}
      {howBehaved === "nice" && <NiceChecked />}
      {howBehaved === "veryNice" && <VeryNiceChecked />}
    </HowBehavedSection>
  );
}

export default HowBehaved;
